*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-size: 12px;
  color: #666;
  line-height: 1; }

.container {
  display: flex;
  flex-direction: column;
  width: 200px; }
  .container .main {
    padding: 5px 0; }
    .container .main .item {
      display: flex;
      padding: 5px 10px; }
      .container .main .item .name {
        width: 40px; }
      .container .main .item .value {
        flex: 1;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        cursor: pointer;
        color: #23ade5; }
  .container .donate {
    border-top: 1px solid #e6e6e6; }
    .container .donate img {
      opacity: .5;
      max-width: 100%; }
  .container .footer {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    cursor: pointer;
    color: #23ade5;
    background: #f7f7f7;
    border-top: 1px solid #e6e6e6; }
